
/*********************
INPUTS
*********************/

// setting up defaults
input[type="text"],
input[type="password"],
input[type="datetime"],
input[type="datetime-local"],
input[type="date"],
input[type="month"],
input[type="time"],
input[type="week"],
input[type="number"],
input[type="email"],
input[type="url"],
input[type="search"],
input[type="tel"],
input[type="color"],
select,
textarea,
.field {

  border: 1px solid #ACACAD;

  @include transition(background-color .24s ease-in-out);



  // error notification
  &.error,
  &.is-invalid {
    color: $alert-red;
    background-color: #fff;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 99% center;
    border-color: $alert-red;
    outline-color: $alert-red;
  }
  // end invalid input

  // success notification
  &.success,
  &.is-valid {
    color: $alert-green;
    background-color: $white;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 99% center;
    border-color: $alert-green;
    outline-color: $alert-green;
  }
  // end valid input

  // a disabled input
  &[disabled],
  &.is-disabled {
    cursor: not-allowed;
    border-color: #000;
    opacity: .6;

    &:focus,
    &:active {
      background-color: $alert-blue;
    }
    // end disabled focus
  }
  // end disabled input
}

// end input defaults

// spacing out the password (thx @murtaugh)
// textareas
textarea {
  max-width: 100%;
  line-height: 1.5em;
}
// selects
select {
  -webkit-appearance: none; /* 1 */
  // Base64 encoded little arrow
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 98.5% center;
}
